.firstDaniel{
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: space-around;
    background-color: rgb(21, 13, 64);
    background-color: #00002A;
}

.firstDaniel .textcontainer{
    width: 30vw;
}

.firstDaniel .textcontainer h1{
    font-size: 3vw;
}

.firstDaniel .textcontainer p{
    font-size: 300%;
}

.firstDaniel .textcontainer a{
    color: white;
    font-size: 140%;
}

.secondDaniel{
    display: flex;
    justify-content: end;
    height: fit-content;
    background: linear-gradient(#270040A1, #1F0051AB, #24015A, #2A0760, #49237F, #2F0363, #180030);
}

.secondDaniel .secondTextcontainer{
    width: 45vw;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 1rem;
    margin-right: 10rem;
}

.secondDaniel .secondTextcontainer p{
    font-size: 300%;
}

.secondDaniel .secondTextcontainer h1{
    font-size: 3vw;
}

@media only screen and (max-width: 900px) {
    .firstDaniel{
        justify-content: flex-start;
    }

    .firstDaniel .textcontainer{
        width: 50vw;
    }
}